import Router from 'vue-router'
import Vue from 'vue'

Vue.use(Router)

const Login = () => import('./views/Login.vue')
const NotFound = () => import('./views/404.vue')
const Home = () => import('./views/Home.vue')
const message = () => import('./views/nav4/message.vue')
const products = () => import('./views/nav1/products.vue')
const protype = () => import('./views/nav1/protype.vue')
const user = () => import('./views/nav2/user.vue')
const sites = () => import('./views/nav3/sites.vue')
const addchild = () => import('./views/nav5/addchild.vue')

const router = new Router({
	mode: 'hash',
	base: '/dist/',
	routes: [
        {
            path: '/login',
            component: Login,
            name: '',
            hidden: true
        },
        {
            path: '/404',
            component: NotFound,
            name: '',
            hidden: true
        },
        {
            path: '/',
            component: Home,
            name: '产品管理',
            iconCls: 'el-icon-goods',//图标样式class
            leaf: false,//只有一个节点
            children: [
            { path: '/', component: products, name: '产品管理' },
            { path: '/protype', component: protype, name: '产品类型' }
            ]
        },
        {
            path: '/',
            component: Home,
            name: '用户数据',
            iconCls: 'el-icon-tickets',
            leaf: true,//只有一个节点
            children: [
                { path: '/user', component: user, name: '用户数据' }
            ]
        },
        {
            path: '/',
            component: Home,
            name: '社区管理',
            iconCls: 'el-icon-location-outline',
            leaf: true,//只有一个节点
            children: [
                { path: '/sites', component: sites, name: '社区管理' }
            ]
        },
        {
            path: '/',
            component: Home,
            name: '基础信息',
            iconCls: 'el-icon-setting',
            leaf: true,//只有一个节点
            children: [
                { path: '/message', component: message, name: '基础信息' }
            ]
        },
        {
              path: '/',
              component: Home,
              name: '添加权限',
              iconCls: 'el-icon-news',
              leaf: true,//只有一个节点
              children: [
                  {
                    path: '/addchild',
                    component: addchild,
                    name: '添加权限'
                  }
              ]
          },
        {
            path: '*',
            hidden: true,
            redirect: { path: '/404' }
        }
    ]
})
router.beforeEach((to, from, next) => {
    // 判断是否是admin主账号登录  主账号显示账号管理，其他账号不显示
    if (sessionStorage.getItem('userId') === null || sessionStorage.getItem('userId') === '1') {
        next();
    } else {
        router.options.routes.map((item) => {
            if (item.name === '添加权限') {
                item.hidden = true
            }
        })
        next();
    }

 })
export default router



